class BaseValidator{
    constructor(element) {
        console.log(`BaseValidator`)
        this.element = element


        this.setup()
    }

    setup() {

        this.element.addEventListener(`change`, this.handleChange)
    }

    handleChange = (evt) => {
        const el = evt.target
        const newValue = el.value
        console.log(`handleChange`, newValue)
    }
}